<template>
  <div class="part-three">
    <h3 class="text-center text-top">个人能力及特长</h3>
    <p class="text-small"><span class="text-small-pot">·</span>熟悉Div + CSS布局，对HTML5和CSS3有深入了解，熟练JavaScript。</p>
    <p class="text-small"><span class="text-small-pot">·</span>掌握Vue.js的开发框架，了解与后端的交互。</p>
    <p class="text-small"><span class="text-small-pot">·</span>对主流浏览器的兼容问题有深入了解。</p>
    <p class="text-small"><span class="text-small-pot">·</span>熟悉并掌握微信小程序的开发。</p>
    <p class="text-small"><span class="text-small-pot">·</span>热爱新技术，责任心强，获得多个优秀称号。</p>
    <p class="text-small"><span class="text-small-pot">·</span>抗压能力强，擅于与同事沟通。</p>
    
  </div>
</template>

<script>
export default {
  name: "partThree",
  data() {
    return {
      
    };
  },
  
};
</script>

<style scoped>
@-webkit-keyframes masked-animation {
    0% {
            background-position: 0 0
        }
    to {
            background-position: -100% 0
        }
}
@keyframes masked-animation {
    0% {
            background-position: 0 0
        }
    to {
            background-position: -100% 0
        }
}



.part-three {
  width: 100%;
  height: 450px;
  border: 40px solid transparent;
  border-image: url("~@/./assets/img/border_image.png") 30 30 stretch;
  background: #18202d;
  text-align: left;
}

.text-center {
    text-align: center;
    margin-bottom: 30px;
    color: hsla(0, 0%, 100%, .65);
    font-weight: 800;
    background-image: -webkit-linear-gradient(left, #00bfff, #207ffc 25%, #00eeff 50%, #0066ff 75%, #00ffdd);
    -webkit-text-fill-color: transparent;
    -webkit-background-clip: text;
    background-clip: text;
    -webkit-background-size: 200% 100%;
    background-size: 200% 100%;
    -webkit-animation: masked-animation 2s infinite linear;
    animation: masked-animation 2s infinite linear;

}
.text-center:hover {
     -webkit-animation: masked-animation 0.5s infinite linear;
     animation: masked-animation 0.5s infinite linear;
     cursor: pointer;
}
.text-small {
    line-height: 30px;
    font-size: 0.9em;
    color: rgb(253, 239, 239);
}
.text-small-pot {
    color: deepskyblue;
}

</style>


